.root {
  // border: 2px solid rgba(0, 0, 0, 0.2);
  position: relative;
  left: -1px;
  border: 1px solid black;
  background-clip: padding-box;
  border-radius: 4px;
  width: 42px;
  background: #fff;
  // background: rgba(255,255,255,.8);
  ul {
    padding: 0;
    margin: 0;
    li {
      list-style: none;
      // border: 1px solid black;
      display: flex;
      height: 34px;
      align-items: center;
      justify-content: center;
      font-size: 20px;
      border-bottom: 1px solid #ccc;
      cursor: pointer;
      color: rgba(0, 0, 0, 0.35);
      &.toggle {
        height: 20px;
        border-bottom: none;
        button {
          outline: none;
          background: transparent;
          border: none;
          padding: 0;
          // line-height: 0;
          // background: orange;
          width: 100%;
          height: 20px;
          display: flex;
          align-items: center;
          justify-content: center;
          // color: #000;
          i {
            line-height: 0;
          }
        }
      }
    }
  }
}
.inactive {
  color: #888 !important;
}
